import React, { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import * as yup from 'yup';
import MainCard from '../../../ui-component/cards/MainCard';
import useStyles from './styles';
import { withStyles } from '@material-ui/styles';
import { IconSearch } from '@tabler/icons';
import HistoryIcon from '@material-ui/icons/History';
import { useLocation } from 'react-router-dom';
import {
    Grid,
    IconButton,
    Typography,
    Button,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    TablePagination,
    OutlinedInput,
    InputAdornment,
    TextField
} from '@material-ui/core';
import { FormProvider, useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import { getViewHistory } from '../../../services/ViewHistory';
import { setMessage } from '../../../store/actions/MessageActions';

const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(even)': {
            backgroundColor: theme.palette.action.hover
        }
    }
}))(TableRow);

const validationSchema = yup.object({
    startDepositDate: yup.string().required('Start Date is required'),
    endDepositDate: yup.string().required('End Date is required')
});

function ViewHistory() {
    const location = useLocation();
    const dispatch = useDispatch();
    const [isSearch, setSearch] = useState('');
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(25);
    const classes = useStyles();

    const [startDate, setStartDate] = useState(new Date().toISOString().substring(0, 10));
    const [endDate, setEndDate] = useState(new Date().toISOString().substring(0, 10));

    const initialValues = {
        startDepositDate: '',
        endDepositDate: ''
    };

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(+event.target.value);
    };

    const handleSearch = (event) => {
        setSearch(event?.target?.value);
    };

    const [viewHistoryData, setViewHistoryData] = useState([]);
    const id = location?.state?.id;

    const GetViewHistory = async () => {
        await getViewHistory(id, page + 1, rowsPerPage, isSearch, startDate, endDate, dispatch, setMessage)
            .then((res) => {
                setViewHistoryData(res?.content);
            })
            .catch((error) => {});
    };

    const methods = useForm({
        mode: 'onChange',
        resolver: yupResolver(validationSchema),
        defaultValues: initialValues
    });
    const { reset, handleSubmit } = methods;

    useEffect(() => {
        GetViewHistory(dispatch, setMessage);
    }, [page, rowsPerPage, isSearch, startDate, endDate]);

    const values = {
        someDate: new Date().toISOString().substring(0, 10)
    };

    const onSubmit = async (data) => {
        getViewHistory(id, page + 1, rowsPerPage, isSearch ? isSearch : '', startDate, endDate, dispatch, setMessage, [
            page,
            rowsPerPage,
            isSearch,
            startDate,
            endDate
        ]);
    };

    return (
        <>
            <MainCard style={{ margin: '20px' }}>
                <div className={classes.titleContainer}>
                    <HistoryIcon className={classes.viewIcon} />
                    <span className={classes.pageTitle}>&nbsp; View History </span>
                </div>
                <Paper elevation={5} className={classes.root}>
                    <Grid style={{ display: 'flex', flexDirection: 'column' }}>
                        <div style={{ display: 'flex', flexDirection: 'row', textAlign: 'justify', padding: '4px' }}>
                            <Typography style={{ fontWeight: 'lighter' }}>Full Name(Profile Id): </Typography>
                            <Typography style={{ fontWeight: 'lighter', color: '#00008b' }}>
                                {location?.state?.fullName}({location?.state?.profileId})
                            </Typography>
                        </div>
                        <div style={{ display: 'flex', flexDirection: 'row', textAlign: 'justify', padding: '4px' }}>
                            <Typography style={{ fontWeight: 'lighter' }}>Email: </Typography>
                            <Typography style={{ fontWeight: 'lighter', color: '#00008b' }}>{location?.state?.email}</Typography>
                        </div>
                    </Grid>
                    <br />

                    <Grid container style={{ display: 'flex', justifyContent: 'space-between' }}>
                        <Grid item xs={12} sm={12} md={4}>
                            <OutlinedInput
                                className={classes.searchControl}
                                id="input-search-header"
                                onChange={(event) => handleSearch(event)}
                                placeholder="Search On STATUS"
                                startAdornment={
                                    <InputAdornment position="start">
                                        <IconSearch stroke={1.5} size="1rem" className={classes.startAdornment} />
                                    </InputAdornment>
                                }
                            />
                        </Grid>

                        <Grid item md={6}>
                            <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
                                <Grid style={{ display: 'flex', justifyContent: 'space-around' }}>
                                    <div>
                                        From: &nbsp;
                                        <TextField
                                            type="date"
                                            name="startDepositDate"
                                            defaultValue={values?.someDate}
                                            onChange={(e) => setStartDate(e?.target?.value)}
                                        />
                                        &nbsp; To: &nbsp;
                                        <TextField
                                            type="date"
                                            name="endDepositDate"
                                            defaultValue={values?.someDate}
                                            onChange={(e) => setEndDate(e?.target?.value)}
                                        />
                                    </div>
                                    <Grid item>
                                        <Button
                                            variant="contained"
                                            className={classes.btn}
                                            onClick={onSubmit}
                                            type="submit"
                                            style={{ alignItems: 'center' }}
                                        >
                                            View
                                        </Button>
                                    </Grid>
                                </Grid>
                            </FormProvider>
                        </Grid>
                    </Grid>
                    <TableContainer className={classes.tableContainer}>
                        <Table className={classes.table} aria-label="simple table">
                            <TableHead className={classes.tableHeaderCell}>
                                <StyledTableRow>
                                    <TableCell> Deposit</TableCell>
                                    <TableCell> Status</TableCell>
                                    <TableCell> Deposit Date</TableCell>
                                    <TableCell> Deduction</TableCell>
                                    <TableCell align="right"> Deduction Comment </TableCell>
                                </StyledTableRow>
                            </TableHead>
                            {!viewHistoryData ? (
                                <caption style={{ textAlign: 'center' }}> Sorry! No data available</caption>
                            ) : (
                                <TableBody>
                                    {viewHistoryData?.map((row, index) => (
                                        <StyledTableRow key={index}>
                                            <TableCell> {row?.deposit}</TableCell>
                                            <TableCell> {row?.status} </TableCell>
                                            <TableCell>{row?.depositDate}</TableCell>
                                            <TableCell>{row?.deduction}</TableCell>
                                            <TableCell align="right">{row?.deductionComment}</TableCell>
                                        </StyledTableRow>
                                    ))}
                                </TableBody>
                            )}
                        </Table>
                    </TableContainer>
                    <TablePagination
                        rowsPerPageOptions={[25, 50, 100]}
                        component="div"
                        count={viewHistoryData?.totalElements || 0}
                        rowsPerPage={rowsPerPage}
                        page={page}
                        onPageChange={handleChangePage}
                        onRowsPerPageChange={handleChangeRowsPerPage}
                        sx={{
                            '.MuiTablePagination-displayedRows': {
                                marginBottom: 0
                            },
                            '.MuiTablePagination-selectLabel': {
                                marginBottom: 0
                            }
                        }}
                    />
                </Paper>
            </MainCard>
        </>
    );
}

export default ViewHistory;
