import { http } from '../utils/http';
import axios from 'axios';
import { setMessage } from '../store/actions/MessageActions';

export const getViewHistory = (
    id,
    page = '1',
    rowsPerPage = 10,
    searchData = '',
    startingDate = '',
    endingDate = '',
    dispatch,
    setMessage
) => {
    return new Promise((resolve, reject) => {
        http(
            process.env.REACT_APP_ROOT_URL_SUBSCRIPTIONS,
            'GET',
            `subscriptions-service/library-security-deposits/${id}/history?pageNumber=${page}&pageSize=${rowsPerPage}&searchKey=${searchData}&startDepositDate=${startingDate}&endDepositDate=${endingDate}`,
            ``,
            true
        )
            .then((data) => {
                resolve(data);
                dispatch(setMessage(data?.message));
            })
            .catch((err) => {
                reject(err);
                dispatch(setMessage(err?.data?.message ? err?.data?.message : 'Invalid Date!'));
            });
    });
};
